import { all, takeLatest } from 'redux-saga/effects';
import { fetchPlayersRequest, searchPlayersRequest } from './players';

export default function* rootSaga(){
    return yield all([
        takeLatest('FETCH_PLAYERS_REQUEST', fetchPlayersRequest),
        takeLatest('SEARCH_PLAYERS_REQUEST', searchPlayersRequest)
    ])
}


