import React, { Component } from 'react';
import { colors } from '../styles/index';
import PlayerItem from './PlayerItem'
import FaSearch from 'react-icons/lib/fa/search';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as PlayersActions from '../store/actions/players';
import * as PageActions from '../store/actions/page';

const styles = {
    container: {
        marginTop: 63,
        backgroundColor: colors.secondary,
        padding: 20,
        flex: 1,
    } ,
    buttonFilter: {
        margin: '20px',
    },
}

class Players extends Component {

    componentDidMount() {
       this.props.fetchPlayersRequest(this.props.page)
    }

    viewNext = async() => {
        await this.props.updatePage(this.props.page[0] + 1)
        this.props.fetchPlayersRequest(this.props.page);
    }


    viewPrevious = async() => {
        await this.props.updatePage(this.props.page[0] - 1)
        this.props.fetchPlayersRequest(this.props.page);
    }

    searchPlayers = async() => {
        var name = document.getElementById("searchBar").value;
        await this.props.searchPlayersRequest(name);
    }

    render() {
        return(
            <div style={styles.container} className="d-flex flex-wrap flex-row align-items-center justify-content-center">
                <div className="dropdown m-3">
                    <button className="btn-lg btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        Overall
                    </button>
                    <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a className="dropdown-item" href="#">Action</a>
                        <a className="dropdown-item" href="#">Another action</a>
                        <a className="dropdown-item" href="#">Something else here</a>
                    </div>
                </div>
                <div className="dropdown m-3">
                    <button className="btn-lg btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        Potencial
                    </button>
                    <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a className="dropdown-item" href="#">Action</a>
                        <a className="dropdown-item" href="#">Another action</a>
                        <a className="dropdown-item" href="#">Something else here</a>
                    </div>
                </div>
                <div className="dropdown m-3">
                    <button className="btn-lg btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        Posies
                    </button>
                    <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a className="dropdown-item" href="#">Action</a>
                        <a className="dropdown-item" href="#">Another action</a>
                        <a className="dropdown-item" href="#">Something else here</a>
                    </div>
                </div>
                <div className="d-flex flex-wrap aling-items-center justify-content-center p-3">
                    <div className="input-group">
                        <input style={{ borderRadius: 0 }} id="searchBar" type="text" className="form-control input-lg" placeholder="Nome" />
                        <span className="input-group-btn">
                            <button style={{ borderRadius: 0 }} onClick={() => this.searchPlayers()} className="btn btn-secondary btn-lg" type="button">
                                <FaSearch />
                            </button>
                        </span>
                    </div>
                </div>
                <div className="d-flex flex-wrap flex-row align-items-center justify-content-center">
                    { this.props.data.map(player => <PlayerItem key={player.id} player={player}/>)}
                </div>
                { this.props.page[0] === 1
                    ?     
                        <button onClick={() => this.viewNext()} className="btn btn-lg btn-secondary m-3">Proxmo</button>
                    :
                        <div>
                                <button onClick={() => this.viewPrevious()} className="btn btn-lg btn-secondary m-2">Anterior</button>
                                <button onClick={() => this.viewNext()} className="btn btn-lg btn-secondary m-2">Proxmo</button>
                        </div>
                }
            </div>
        );
    }
}

const mapStateToProps = state => ({
    data: state.players,
    page: state.page,
})

const mapDispatchToProps = dispatch =>
    bindActionCreators({...PlayersActions, ...PageActions}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Players);


