const initialState = [];

export default function players(state=initialState, action){
    switch(action.type) {
        case "FETCH_PLAYERS_SUCCESS":
            return [...action.payload.data];
        case "SEARCH_PLAYERS_SUCCESS":
            return [...action.payload.data];
        default:
            return state;
    }
}


