export const fetchPlayersRequest = page => {
    return {
        type: "FETCH_PLAYERS_REQUEST",
        payload: {
            page,
        }
    };
};

export const fetchPlayersSuccess = data => {
    return {
        type: "FETCH_PLAYERS_SUCCESS",
        payload: {
            data,
        }
    };
};

export const searchPlayersRequest = playerName => {
    return {
        type: "SEARCH_PLAYERS_REQUEST",
        payload: {
            playerName,
        }
    };
};

export const searchPlayersSuccess = data => {
    return {
        type: "SEARCH_PLAYERS_SUCCESS",
        payload: {
            data,
        }
    };
};


